<template>
	<div id="authentication">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">认证</div>
      </div>
    </div>
    <div class="login_box text_center">
      <div style="margin-top: 100px">
        <img src="static/img/icon_cjrz_rz1.png" style="width: 30%;height: 30%;" @click="toFirstAuthentication"/>
        <div class="margin_top_10">初级认证</div>
      </div>
      <div style="margin-top: 50px">
        <img src="static/img/icon_222.png" style="width: 30%;height: 30%;" @click="toSeniorAuthentication"/>
        <div class="margin_top_10">高级认证</div>
        <div class="font_12 color_8b8b8b margin_top_10">(获得高级认证才可以招聘)</div>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'firstAuthentication',
		data() {
			return {
        userId:localStorage.getItem('userId'),
			}
		},
		methods:{
      toFirstAuthentication(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("primary/isprimary", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='555'||rpdata.data.meta.code=='888'){
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
            $this.$router.push('firstAuthentication');
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      toSeniorAuthentication(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("senior/issenior", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='555'||rpdata.data.meta.code=='888'){
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
            $this.$router.push('seniorAuthentication');
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_box{
    margin-top: 50px;
  }
</style>



